  export default {
    name: 'AddressIndex',
    data (){
      return {
        checkedTrue: true,
        checkedFalse: false,
        list: []
      }
    },
    methods: {
      //删除
      deleteAddress(id,index){
        this.$createDialog({
          type: 'confirm',
//          icon: 'cubeic-alert',
          title: '您确认删除该地址吗',
          confirmBtn: {
            text: '确定',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            // 显示
            this.$vux.loading.show({
              text: 'Loading'
            })
            this.http.delete(this.ports.me.address+'/'+id, res =>{
              if(this.$vux.loading.isVisible()){
                // 隐藏
                this.$vux.loading.hide()
              }
              console.log(res)
              if(res.success){
                this.list.splice(index, 1)
              }else{
                this.showToastTxtOnly(res.msg)
              }
            })
          }
        }).show()

      },
      //设置默认地址
      defaultClick(item){
        this.list.forEach((even,i)=>{
          even.is_default = 0
        })
        item.is_default = 1
        this.http.put(this.ports.me.address+'/'+item.id,{is_default: 1}, res =>{
          if(res.success){
//            let data = res.data
//            this.list = data.res
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      //获取列表数据
      getAddressData(){
        this.http.get(this.ports.me.address, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.list = data.res
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      }
    },
    mounted (){
      this.getAddressData()
    }
  }



